.page{
    .sidebar{
        width: 250px;
        position: fixed;
        top: 0;
        left: 0;
        color: white;
        background-color: #292c33;
        padding: 15px;
        height: 100%;
        transition-duration: 0.3s;
        &.no-sidebar{
            left: -250px;
        }
    }
}